.today--section {
    margin-bottom: 40px;
    padding: 0 50px;
}
.today--section h2 {
    font-size: 20px;
}
.today--section__box {
    background-color: white;
    padding: 20px;
    margin: 20px 0;
    border-radius: 4px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}
.btc--section {
    text-align: center;
    border-right: 1px solid #DAE1E9;
   display: flex;
   justify-content: space-around;
}
.btc--section h5 {
    font-size: 30px;
}
.eth--section {
    text-align: center;
    border-right: 1px solid #DAE1E9;
    display: flex;
   justify-content: space-around;
}
.eth--section h5 {
    font-size: 30px;
}
.ltc--section {
    text-align: center;
    display: flex;
   justify-content: space-around;
}
.ltc--section h5 {
    font-size: 30px;
}
p span{
    color:rgb(87, 0, 228);
    font-weight: 600;
}
.up-arrow {
	border-color: rgb(15, 177, 0) transparent;
	border-style: solid;
	border-width: 0px 15px 20px 15px;
	height: 0px;
	width: 0px;
}
.down-arrow {
	border-color: rgb(255, 8, 0) transparent;
	border-style: solid;
	border-width: 20px 15px 0px 15px;
	height: 0px;
    width: 0px;
    
}
.disabled{
    display: none;
}
.column{
    display: flex;
}
.upordown{
    margin: auto 1px;
}
@media (max-width: 480px) {
    .eth--section {
        border-right: none;
    }
    .btc--section {
        border-right: none;
    }
    .today--section {
        margin-top:  50px;
    }
}